<template>
  <div id="index">
    <Row>
      <Col span="12" class="header">
        <Card :bordered="false" :padding=0>
          <div class="title">
            <img src="../assets/A2.png">
            <h1>{{sys.name}}</h1>
            <time>{{today}}</time>
          </div>
          <svg class="waves" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink"
               viewBox="0 24 150 28" preserveAspectRatio="none" shape-rendering="auto">
            <defs>
              <path id="gentle-wave" d="M-160 44c30 0 58-18 88-18s 58 18 88 18 58-18 88-18 58 18 88 18 v44h-352z"/>
            </defs>
            <g class="parallax">
              <use xlink:href="#gentle-wave" x="48" y="0" fill="rgba(255,255,255,0.7"/>
              <use xlink:href="#gentle-wave" x="48" y="3" fill="rgba(255,255,255,0.5)"/>
              <use xlink:href="#gentle-wave" x="48" y="5" fill="rgba(255,255,255,0.3)"/>
              <use xlink:href="#gentle-wave" x="48" y="7" fill="#fff"/>
            </g>
          </svg>
        </Card>
      </Col>
    </Row>
    <Row>
      <Col span="12" class="countdown">
        <h1>倒计时</h1>
        <p>
          每天新进资金的8.4%按照智能合约自动滚入奖池，累计达到30000个ETH后，每进场1个ETH就延长3小时开奖，倒计时24小时计算，没有资金进场就开奖，100%奖励给最后300名大单得主，24小时后自动重启新一轮。
        <div class="big">
          <img src="../assets/eth.png" alt="">
          <p>大奖池</p>
          <time></time>
          <span>{{contractInfo.bigJackpot}} ETH</span>
        </div>
        <div class="small">
          <img src="../assets/eth.png" alt="">
          <p>小奖池</p>
          <time>{{smallTime}}</time>
          <span>{{contractInfo.smallJackpot}} ETH</span>
        </div>

      </Col>
    </Row>
    <Row>
      <Col span="12" class="involvement">
        <Divider class="title">我要参与</Divider>
        <Card class="card" :padding=0 :bordered="false">
          <Tabs class="tabs">
            <TabPane label="参与">
              <div class="inPlan">
                <p>当前燃烧比例 <span>1ETH = {{contractInfo.consume}}TTS</span></p>
                <div class="btnGroup">
                  <ButtonGroup>
                    <Button ghost @click="inPlanAmount=1">1~10ETH</Button>
                    <Button ghost @click="inPlanAmount=11">11~30ETH</Button>
                    <Button ghost @click="inPlanAmount=31">31ETH</Button>
                  </ButtonGroup>
                </div>
                <Input class="input" v-model.number="inPlanAmount"><span slot="append">ETH</span></Input>
                <Button type="success" long @click="buyPlan">参与</Button>
              </div>
            </TabPane>
            <TabPane label="火种">
              <div class="ticket">
                <p>购买入场火种 <span>1ETH = {{contractInfo.rate}}TTS</span></p>
                <Input class="input" v-model.number="rateAmount"><span slot="append">ETH</span></Input>
                <Button type="success" long @click="buyRate">购买</Button>
              </div>
            </TabPane>
            <TabPane label="邀请">
              <div class="inTeam">
                <p v-if="user&&user.sup">我的推薦人是:
                  <span :style="{color:tjrColor}">{{user.sup.addr | subString(4)}}</span>
                </p>
                <p v-else>我的推薦人是: --</p>
                <p>我的直推數量: {{user&&user.sup?user.sub.length:'--'}}</p>
                <p v-if="user">我的邀請鏈接: {{user.addr| subString(4)}}</p>
                <p v-else>我的邀請鏈接: --</p>
                <Button type="success" long @click="doCopy">复制我的邀请链接</Button>
              </div>
            </TabPane>
            <TabPane label="规则">
              <ul class="rule">
                <li>
                  <Icon type="ios-star"/>
                  壹個賬號在出局前僅接受壹筆投資，出局後可繼續投資
                </li>
                <li>
                  <Icon type="ios-star"/>
                  靜態分紅按照每天0.35%~2.5%上下浮動
                </li>
                <li>
                  <Icon type="ios-star"/>
                  動靜出局，按照1-10為投資額3倍，11-30為投資額4倍，大於31為投資額5倍
                </li>
                <li>
                  <Icon type="ios-star"/>
                  動態推薦26%按照:12%->8%->6%獲取動態獎勵，可即時提現
                </li>
                <li>
                  <Icon type="ios-star"/>
                  小獎池達到指定時間後，小獎池不再沉澱，直到開啟下壹輪
                </li>
                <li>
                  <Icon type="ios-star"/>
                  大獎池達到指定時間後，不再沉澱，投資額直接進入分紅收益
                </li>
                <li>
                  <Icon type="ios-star"/>
                  動態如果未達到條件，沉澱至分紅池
                </li>
              </ul>
            </TabPane>
          </Tabs>
        </Card>
      </Col>
    </Row>
    <Row>
      <Col span="12" class="prize">
        <Divider class="title">幸运大转盘</Divider>
        <Card class="card">
          <div class="lucky-wheel">
            <div class="wheel-main">
              <div class="wheel-pointer-box">
                <div class="wheel-pointer" @click="rotate_handle()" :style="{transform:rotate_angle_pointer,transition:rotate_transition_pointer}"></div>
              </div>
              <div class="wheel-bg" :style="{transform:rotate_angle,transition:rotate_transition}">
                <div class="prize-list">
                  <div class="prize-item" v-for="(item,index) in prize_list" :key="index">
                    <div class="prize-pic"><img :src="item.icon"></div>
                    <div class="prize-count" v-if="item.count">{{item.count}}</div>
                  </div>
                </div>
              </div>
            </div>
          <p>可抽奖次数：{{lottery_ticket}}</p>
          </div>
        </Card>
      </Col>
    </Row>
    <Row>
      <Col span="12" class="logs">
        <Divider class="title">统计数据</Divider>
        <Card class="card infoList " :padding=0 :bordered="false">
          <p slot="title">我的收益</p>
          <p><span class="label">已結算分紅收益</span> <span class="extra">{{user | yStatic | flex(4)}} ETH</span></p>
          <p><span class="label">奖池收益</span> <span class="extra">{{user.jackpot |flex(4)}} ETH</span></p>
<!--          <p><span class="label">下次分红收益时间差</span> <span class="extra">{{smallTime}}</span></p>-->
          <p><span class="label">动静出局还差</span> <span class="extra">{{user| outNum | flex(4)}} ETH</span></p>
          <p><span class="label">3倍未结算预计收益</span> <span class="extra">{{user.static3 | flex(4)}} ETH <Button
            type="success" size="small" @click="settle('static3',user.static3)">提现</Button></span></p>
          <p><span class="label">4倍未结算预计收益</span> <span class="extra">{{user.static4 | flex(4)}} ETH <Button
            type="success" size="small" @click="settle('static4',user.static4)">提现</Button></span></p>
          <p><span class="label">5倍未结算预计收益</span> <span class="extra">{{user.static5 | flex(4)}} ETH <Button
            type="success" size="small" @click="settle('static5',user.static5)">提现</Button></span></p>
          <p><span class="label">推介奖励</span> <span class="extra">{{user.dynamic | flex(4)}} ETH <Button type="success"
                                                                                                        size="small"
                                                                                                        @click="settle('dynamic',user.dynamic)">提现</Button></span>
<!--          <p><span class="label">未提币数量</span> <span class="extra">{{user.daysAmount | flex(4)}} ETH </span></p>-->
            <Button type="success" long @click="settleALL" :disabled="user.static3<=0&&user.static4<=0&&user.static5<=0&&user.dynamic<=0&&user.shareholder<=0&&user.angelAmount<=0">提 现 收 益</Button>
          </p>
        </Card>
        <Card class="card infoList " :padding=0 :bordered="false">
          <p slot="title">统计</p>
          <p><span class="label">大奖池当前总量</span> <span class="extra">{{contractInfo.bigJackpot | flex(4)}} ETH </span>
          </p>
          <p><span class="label">小奖池当前总量</span> <span class="extra">{{contractInfo.smallJackpot | flex(4)}} ETH </span>
          </p>
          <p><span class="label">火种焚毁倍率</span> <span class="extra">{{contractInfo.consume}} TIMES </span></p>
          <p><span class="label">已焚毁火种</span> <span class="extra">{{contractInfo.totalConsume}} TTS </span></p>
          <p><span class="label">已投入的总资金</span> <span class="extra">{{user.invest | flex(4)}} ETH </span></p>
          <p><span class="label">总的推介收益</span> <span class="extra">{{user.aDynamic | flex(4)}} ETH </span></p>
          <p><span class="label">团队</span> <span class="extra">{{user.shareholder | flex(4)}} ETH <Button type="success"
                                                                                                          size="small"
                                                                                                          @click="settle('shareholder',user.shareholder)">提现</Button></span>
          </p>
          <p><span class="label">全球节点</span> <span class="extra">{{user.angelAmount | flex(4)}} ETH <Button
            type="success" size="small" @click="('angelAmount',user.angelAmount)">提现</Button></span></p>
        </Card>
        <Card class="card jcList" :padding=0 :bordered="false">
          <p slot="title">历史记录</p>
          <p class="big">大奖池</p>
          <p class="small">小奖池</p>
          <CellGroup>
            <Cell v-for="item in jp" v-if="item.isStarted" :title="'Round'+item.num.toString()"
                  :extra="item.amount+' ETH'"/>
          </CellGroup>
          <router-link to="/total" v-if="user">查看我的統計數據</router-link>
          <router-link v-else>查看我的統計數據</router-link>
        </Card>
      </Col>
    </Row>
  </div>
</template>

<script>
    import mixins from '../mixins'
    import moment from 'moment';
    import Bn from 'bignumber.js'
    import * as api from '../api'
    import * as types from '../store/mutation-types'

    export default {
        name: "index",
        mixins: [mixins],
        created(){
            this.init_prize_list();
        },
        data() {
            return {
                msg: [],
                bigTime: null,
                smallTime: null,
                today: moment().format('YYYY.MM.DD'),
                inPlanAmount: 0,
                rateAmount: 0,
                prize_list: [
                    {
                        icon: require("../assets/img/eth.png"), // 奖品图片
                        count: 0.01, // 奖品数量
                        isPrize: 1 // 该奖项是否为奖品
                    },
                    {
                        icon: require("../assets/img/eth.png"),
                        count: 0.02,
                        isPrize: 1
                    },
                    {
                        icon: require("../assets/img/eth.png"),
                        count: 0.1,
                        isPrize: 1
                    },
                    {
                        icon: require("../assets/img/eth.png"),
                        count: 0.5,
                        isPrize: 1
                    },
                    {
                        icon: require("../assets/img/eth.png"),
                        count: 1,
                        isPrize: 1
                    },
                    {
                        icon: require("../assets/img/eth.png"),
                        count: 5,
                        isPrize: 1
                    },
                    {
                        icon: require("../assets/img/eth.png"),
                        count: 10,
                        isPrize: 1
                    },
                    {
                        icon: require("../assets/img/eth.png"),
                        count: 50,
                        isPrize: 1
                    }
                ], //奖品列表
                start_rotating_degree: 0, //初始旋转角度
                rotate_angle: 0, //将要旋转的角度
                start_rotating_degree_pointer: 0, //指针初始旋转角度
                rotate_angle_pointer: 0, //指针将要旋转的度数
                rotate_transition: "transform 6s ease-in-out", //初始化选中的过度属性控制
                rotate_transition_pointer: "transform 12s ease-in-out", //初始化指针过度属性控制
                click_flag: true, //是否可以旋转抽奖
                index: 0,
                lottery_ticket:0
            }
        },

        filters: {
            flex(e, r) {
                if (e) {
                    return e.toFixed(r)
                } else {
                    return e;
                }
            },
            outNum(e) {
                let n = new Bn(e.aStatic3).plus(e.aStatic4).plus(e.aStatic5).toNumber();
                let c = [e.investGroup[0] * 3, e.investGroup[1] * 4, e.investGroup[2]];
                let s = new Bn(c[0]).plus(c[1]).plus(c[2]).toNumber();
                return s - n;
            },
            yStatic(e){
                return new Bn(e.aStatic3).plus(e.aStatic4).plus(e.aStatic5).toNumber();
            },
            subString(e, r) {
                if(e){
                    return e.substring(0, 16) + '...' + e.substring(e.length - r)
                }else{
                    return ''
                }
            }
        },
        computed: {
            url() {
                if (this.user) {
                    return `${this.sys.url}/?sup=${this.user.addr}`
                } else {
                    return `${this.sys.url}/`
                }
            },
            tjrColor() {
                if (this.user._id && this.user.invest > 0) {
                    return '#ed4014'
                } else {
                    return "#19be6b"
                }
            },

        },
        async mounted() {
            // 初始化
            this.countTime();
            let that = this;
            let lt = await api.getLotteryTicket();
            this.lottery_ticket = lt.data;
            this.sockets.subscribe('reload', async data => {
                if (data.reload) {
                    that.init();
                    let user = await api.getUser();
                    that.$store.commit(types.UPDATE_USER, user.user);
                }
            });
        },
        methods: {
            //此方法为处理奖品数据
            init_prize_list(list) {},
            async rotate_handle() {
                if(this.click_flag&&this.lottery_ticket>0){
                    let i = await api.getPrizeIndex();
                    this.index = i.data; //指定每次旋转到的奖品下标
                    this.rotating();
                }
            },
            rotating() {
                let that = this;
                let during_time = 5; // 默认为1s
                let random = Math.floor(Math.random() * 7);
                let result_index = this.index ; // 最终要旋转到哪一块，对应prize_list的下标
                let result_angle = [337.5, 292.5, 247.5, 202.5, 157.5, 112.5, 67.5, 22.5]; //最终会旋转到下标的位置所需要的度数
                let  rand_circle = 6; // 附加多转几圈，2-3
                this.click_flag = false; // 旋转结束前，不允许再次触发
                // 转动盘子
                let rotate_angle =
                    this.start_rotating_degree +
                    rand_circle * 360 +
                    result_angle[result_index] -
                    this.start_rotating_degree % 360;
                this.start_rotating_degree = rotate_angle;
                this.rotate_angle = "rotate(" + rotate_angle + "deg)";
                // 旋转结束后，允许再次触发
                setTimeout(async function() {
                    let user = await api.getUser();
                    that.$store.commit(types.UPDATE_USER, user.user);
                    that.click_flag = true;
                    that.game_over();
                }, during_time * 1000 + 1500); // 延时，保证转盘转完
            },
            game_over() {
                this.$Message.success("恭喜您，获得" +this.prize_list[this.index].count + " ETH");
            },
            // 初始化
            async init() {
                let that = this;
                this.$store.dispatch('initService');
                this.countTime();
                let lt = await api.getLotteryTicket();
                this.lottery_ticket = lt.data;
                setTimeout(function () {
                    that.$store.dispatch('newContract');
                }, 5000)
            },
            // 结算
            async settle(e, r) {
                if (r > 0) {
                    this.$store.commit(types.LOADDING, true);
                    let a = await api.settle({type: e, amount: r});
                    if (a.success) {
                        let user = await api.getUser();
                        this.$store.commit(types.UPDATE_USER, user.user);
                        this.$store.commit(types.LOADDING, false);
                        this.$Message.success('结算成功！')
                    }
                } else {
                    this.$Message.error('结算失败！')
                }
            },
            async settleALL(e){
                this.$store.commit(types.LOADDING, true);
                let r = new Bn(0).plus(this.user.static3).plus(this.user.static4).plus(this.user.static5).plus(this.user.dynamic)
                    .plus(this.user.shareholder).plus(this.user.angelAmount).toNumber();
                let a = await api.settle({type: 'all', amount: r});
                if (a.success) {
                    let user = await api.getUser();
                    this.$store.commit(types.UPDATE_USER, user.user);
                    this.$store.commit(types.LOADDING, false);
                    this.$Message.success('结算成功！')
                }
            },
            // 复制邀请码
            doCopy() {
                if (!this.user || !this.user._id) return this.$Message.error('您还不是本站会员！');
                if (this.account) this.$copyText(this.url).then(e => this.$Message.success('复制成功！'))
            },

            // 倒计时
            countTime: function () {
                let bTime = new Date(this.sys.bigTime).getTime() - new Date().getTime();
                let sTime = new Date(this.sys.smallTime).getTime() - new Date().getTime();
                if (bTime >= 0) this.bigTime = Math.floor(bTime / 1000 / 60 / 60 % 24) + ':' + (Math.floor(bTime / 1000 / 60 % 60) > 9 ? Math.floor(bTime / 1000 / 60 % 60) : '0' + Math.floor(bTime / 1000 / 60 % 60)) + ":" + (Math.floor(bTime / 1000 % 60) > 9 ? Math.floor(bTime / 1000 % 60) : '0' + Math.floor(bTime / 1000 % 60));
                if (sTime >= 0) this.smallTime = Math.floor(sTime / 1000 / 60 / 60 % 24) + ':' + (Math.floor(sTime / 1000 / 60 % 60) > 9 ? Math.floor(sTime / 1000 / 60 % 60) : '0' + Math.floor(sTime / 1000 / 60 % 60)) + ":" + (Math.floor(sTime / 1000 % 60) > 9 ? Math.floor(sTime / 1000 % 60) : '0' + Math.floor(sTime / 1000 % 60));
                setTimeout(this.countTime, 1000);
            },
            // 购买门票
            buyRate() {
                if (!this.user || !this.user._id) return;
                let that = this;
                if (!this.user) return that.$Message.error('您还不是本站会员！');
                if (isNaN(this.rateAmount) || this.rateAmount < 1 || this.rateAmount % 1 > 0) return this.$Message.error('输入错误！');
                let hashs = null;
                contract.methods.tickets().send({
                    from: this.account,
                    value: web3.utils.toWei(new BN(this.rateAmount), 'ether')
                })
                    .on('transactionHash', async hash => {
                        that.$store.commit(types.LOADDING, true);
                        if (hash) await api.saveHash({hash, type: 1, amount: that.rateAmount});
                        hashs = hash;
                    })
                    .on('confirmation', function (confirmationNumber, receipt) {
                    })
                    .on('receipt', async receipt => {
                        await api.confirmHash({
                            item: {hash: hashs, type: 1, amount: that.rateAmount},
                            status: receipt.status
                        });
                        that.$store.commit(types.LOADDING, false);
                        that.rateAmount = 0;
                        if (receipt.status) that.$Message.success('购买成功！');
                    })
                    .on('error', error => {
                        that.$store.commit(types.LOADDING, false);
                    })
            },
            // 参与计划
            buyPlan() {
                if (!this.user || !this.user._id) return;
                let that = this;
                if (!this.user) return that.$Message.error('您还不是本站会员！');
                if (isNaN(this.inPlanAmount) || this.inPlanAmount < 1 || this.rateAmount % 1 > 0) return this.$Message.error('输入错误！');
                let hashs = null;
                contract.methods.ticketConsume().send({
                    from: this.account,
                    value: web3.utils.toWei(new BN(this.inPlanAmount), 'ether')
                })
                    .on('transactionHash', async hash => {
                        if (hash) await api.saveHash({hash, type: 2, amount: that.inPlanAmount});
                        hashs = hash;
                        that.$store.commit(types.LOADDING, true);
                    })
                    .on('confirmation', function (confirmationNumber, receipt) {
                    })
                    .on('receipt', async receipt => {
                        await api.confirmHash({
                            item: {hash: hashs, type: 2, amount: that.inPlanAmount},
                            status: receipt.status
                        });
                        that.$store.commit(types.LOADDING, false);
                        if (receipt.status) that.$Message.success('参与成功！');
                    })
                    .on('error', error => {
                        that.$store.commit(types.LOADDING, false);
                    })
            },
        }
    }
</script>

<style lang="less">
