      }

      & > p {
        text-indent: 2rem;
        line-height: 1.5rem;
        font-size: .9rem;
        text-align: left;
      }

      img {
        left: 8%;
        z-index: 1;
        opacity: 1;
        top: 40%;
        height: 2.8rem;
      }

      .big {
        color: #c5085b;
        border: 1px solid #C5085B;
      }

      .small {
        color: #bcbc37;
        border: 1px solid #BCBC37;
        margin-bottom: 2rem;
      }

      & > div {
        position: relative;
        width: 80%;
        display: inline-block;
        padding: 10px;
        margin-top: 3rem;
        border-radius: 7px;

        time, span {
          position: relative;
          display: block;
          z-index: 2;
        }

        p {
          font-size: 1.2rem;
        }

        time {
          font-size: 2.4rem;
        }

        span {
          font-size: 2rem;
          line-height: 2rem;
        }
      }
    }

    .involvement {
      width: 100%;

      .card {
        width: 90%;
        margin: auto;
        padding: 0;
        box-shadow: 0 0 10px 3px #00D7BD;
        overflow: hidden;
        background: #2a2a2a;
      }

      .tabs {
        .ivu-tabs-nav-scroll {
          background: #000000;

          .ivu-tabs-nav {
            width: 100%;

            .ivu-tabs-ink-bar {
              background-color: #CA0000;
            }

            .ivu-tabs-tab {
              text-align: center;
              color: #ffffff;
            }

            .ivu-tabs-tab-active {
              color: #CA0000;
            }
          }
        }

        .ivu-tabs-content {
          .inPlan, .inTeam, .ticket, .rule {
            padding: 0 20px 20px;
            color: #ffffff;

            p {
              line-height: 2rem;
              text-overflow: ellipsis;
              overflow: hidden;
              white-space: nowrap;

              span {
                color: #ca0000;
              }
            }
          }

          .inPlan, .ticket {
            p {
              font-weight: 500;
              margin-bottom: 1rem;
              font-size: 1.1rem;

              span {
                font-size: 1.1rem;
              }
            }
          }

          .inPlan, .ticket {
            .btnGroup {
              text-align: center;
              margin-bottom: 1rem;

              .ivu-btn-group {
                border-radius: 4px;
                box-shadow: 0 0 4px 1px #00D7BD;
              }

              button {
                border: 1px solid #00D7BD;
                color: #00d7bd;
                text-shadow: 1px 1px 2px #00D7BD;
              }
            }

            .input {
              margin-bottom: 1rem;
              height: 2.6rem;
              line-height: 2.6rem;

              input {
                text-indent: 1rem;
                height: 2.6rem;
              }
            }
          }

          .rule {
            list-style: none;

            i {
              color: #ffc500;
            }
          }
        }
      }
    }
    .prize{
      width: 100%;
    }
    .logs {
      width: 100%;

      .ivu-card-head {
        padding: 0;

        p {
          border: none !important;
          text-align: center;
          font-size: 1.1rem;
          color: #ffffff;
          line-height: 3rem;
          height: 3rem;
        }
      }

      .card {
        border-radius: 0;
        background: none;

        .ivu-cell + .ivu-cell {
          border-top: 1px solid #008c7c;
        }

        div {
          color: #008c7c;
        }

        .btns .ivu-cell-main {
          width: 100%;
        }
      }

      .infoList {

        p {
          padding: 0 1rem;
          line-height: 40px;

          .extra {
            float: right;
          }
        }
      }

      .jcList {
        margin-bottom: 5em;
        a{
          display: block;
          text-align: center;
          padding: 1rem 0;
        }
        .big {
          color: #c5085b;
        }

        .small {
          color: #f7b500;
        }

        p {
          text-align: center;
          line-height: 3rem;
        }
      }
    }

  }

  .waves {
    position: absolute;
    width: 100%;
    height: 15vh;
    margin-bottom: -7px; /*Fix for safari gap*/
    min-height: 100px;
    max-height: 150px;
    bottom: 0;
  }

  .content {
    position: relative;
    height: 20vh;
    text-align: center;
    background-color: white;
  }

  /* Animation */
  .parallax > use {
    animation: move-forever 25s cubic-bezier(.55, .5, .45, .5) infinite;
    &:nth-child(1) {
      animation-delay: -2s;
      animation-duration: 7s;
    }

    &:nth-child(2) {
      animation-delay: -3s;
      animation-duration: 10s;
    }

    &:nth-child(3) {
      animation-delay: -4s;
      animation-duration: 13s;
    }

    &:nth-child(4) {
      animation-delay: -5s;
      animation-duration: 20s;
    }
  }
  @keyframes move-forever {
    0% {
      transform: translate3d(-90px, 0, 0);
    }
    100% {
      transform: translate3d(85px, 0, 0);
    }
  }

  /*Shrinking for mobile*/
  @media (max-width: 768px) {
    .waves {
      height: 60px;
      min-height: 60px;
    }

    .content {
      height: 30vh;
    }

    h1 {
      font-size: 24px;
    }
  }
  .prize{
    .card{
      border: none;
      background: none;
      p{
        text-align: center;
        color: #008c7c;
      }
      .ivu-card-body{
        padding: 0;
        .lucky-wheel {
          width: 100%;
          height: auto;
          background: none;
          background-size: 100%;
          padding-bottom: 1rem;
          .wheel-main {
            display: flex;
            align-items: center;
            justify-content: center;
            position: relative;
            .wheel-pointer-box {
              position: absolute;
              top: 50%;
              left: 50%;
              z-index: 100;
              transform: translate(-50%, -60%);
              width: 3rem;
              height: 3rem;
              .wheel-pointer {
                width: 3rem;
                height: 3rem;
                background: url("../assets/img/draw_btn.png") no-repeat center top /80%;
                transform-origin: center 60%;
              }
            }
            .wheel-bg {
              width: 18.4375rem;
              height: 18.4375rem;
              background: url("../assets/img/draw_wheel.png") no-repeat center top;
              background-size: 100%;
              color: #fff;
              font-weight: 500;
              display: flex;
              flex-direction: column;
              justify-content: center;
              align-content: center;
              transition: transform 3s ease;
              .prize-list {
                width: 100%;
                height: 100%;
                position: relative;
                .prize-item {
                  position: absolute;
                  top: 0;
                  left: 0;
                  z-index: 2;
                  width: 5.875rem;
                  height: 9rem;
                  .prize-count {
                    margin-top: .3rem;
                    font-size: 1rem;
                  }
                  .prize-pic img {
                    width: 1.6rem;
                    margin-top: 3.2rem;
                    transform: rotate(70deg);
                  }
                  &:first-child {
                    top: 0;
                    left: 8.3125rem;
                    transform: rotate(20deg);
                  }
                  &:nth-child(2) {
                    top: 2.8rem;
                    left: 10.8rem;
                    transform: rotate(67deg);
                  }
                  &:nth-child(3) {
                    top: 6.4rem;
                    left: 10.6rem;
                    transform: rotate(-250deg);
                  }
                  &:nth-child(4) {
                    top: 9rem;
                    left: 8.2125rem;
                    transform: rotate(-210deg);
                  }
                  &:nth-child(5) {
                    top: 9.2125rem;
                    left: 4.4rem;
                    transform: rotate(-160deg);
                  }
                  &:nth-child(6) {
                    top: 6.3875rem;
                    left: 1.9rem;
                    transform: rotate(-111deg);
                  }
                  &:nth-child(7) {
                    top: 2.8rem;
                    left: 1.8125rem;
                    transform: rotate(-69deg);
                  }
                  &:nth-child(8) {
                    top: 0;
                    left: 4.5rem;
                    transform: rotate(-20deg);
                  }
                }
              }
              div {
                text-align: center;
              }
            }
          }
        }
      }
    }
  }
</style>



